import { Component, OnInit, Input } from '@angular/core';
import { Student } from '../../models/student.interface'

@Component({
  selector: 'app-delstudent',
  templateUrl: './delstudent.component.html',
  styleUrls: ['./delstudent.component.scss']
})
export class DelstudentComponent implements OnInit {


  @Input() delStudent:Student;
  @Input() allStudent:Student[];
  constructor() { }

  ngOnInit() {
  }

  delete(){
    var n=this.allStudent.indexOf(this.delStudent);
    this.allStudent.splice(n,1);

  }
}
